import React from 'react';
import PlaceVO from '../models/PlaceVO';

const ClusteritemWindow = (placeVO: PlaceVO) => {
    return (
        `<div id="cluster_item" class="cluster_item">
            <div class="cluster_header">
                <h4 class="txt_cut1"> </h4>
                <a className="filter_popup_close"
                    onClick="closeClusterWindow()"
                ></a>
            </div>
            <h4 class="txt_cut1">${placeVO.place_name}</h4>
            <p class="address">() ${placeVO.road_address_name}</p>
            <p class="address">() ${placeVO.address_name}</p>
            <p class="address">() ${placeVO.phone.length > 0 ? placeVO.phone : ""}</p>
            <p class="address"> : ${placeVO.category_name}</p>
            <a class="url"; id="${placeVO.id}"; onClick="openWinFrame('${placeVO.place_url}')"; ><b></b></a>
        </div>`
    );
};

export default ClusteritemWindow;